import React, {Component} from "react";

export default class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      longitude: 0,
      latitude: 0,
      showICA: false,
      ica: 0
    };
}
  savePosition = (position) => {
    this.setState({
      longitude:position.coords.longitude,
      latitude: position.coords.latitude
    }
    )
    console.log(this.state.longitude)
    this.onCalculateICA()
  }
  onGetPosition = () => {
    navigator.geolocation.getCurrentPosition(this.savePosition)
  }
  onCalculateICA = () => {
    var url = 'http://3.84.253.196'
    //var data = {"longitude": 56421, "latitude": 5432453}
    fetch(url,{
          method: 'GET',
          // body: JSON.stringify(data),
          mode: 'no-cors',
          headers:{
            'Content-Type': 'application/json'
          }
        }).then(res => res.json())
    .catch(error => console.error('Error: ', error))
    .then(res => console.log(res))
  }
  // onCalculateICA = ()=> {
  //   var url = 'http://3.84.253.196/requestICA'
  //   var data = {longitude: this.state.latitude,
  //               latitude: this.state.longitude}
  //   fetch(url, {
  //     method: 'POST',
  //     body: JSON.stringify(data),
  //     mode: 'no-cors',
  //     headers:{
  //       'Content-Type': 'application/json'
  //     }
  //   }).then(res => res.json())
  //   .catch(error => console.error('Error:', error))
  //   .then(response => {
  //     console.log('Success:')
  //   });
  // }
  render() {
    return(
      <section>
        <h1>Medidor de contaminación en el aire</h1>
        <button type="button" onClick={this.onGetPosition}>clicme</button>
        {this.state.latitude > 0 &&
        <ul>
          <li>latitud = {this.state.latitude}.</li>
          <li>Longitud = {this.state.longitude}.</li>
        </ul>
      }
      {this.state.showICA &&
      <p>el valor de la contaminación en tu ubicación es: {this.state.ica}
        </p>}
      </section>
    )
  }
}